import { useState } from "react";
import Form from "./Form";
import Tasks from "./Tasks";
import Buttons from "./Buttons";
import Section from "./Section";
import "./index.css";
import "./container.css";

const tasks = [
  { id: 1, content: "przejÅ›Ä‡ na Reacta", done: true },
  { id: 2, content: "zjeÅ›Ä‡ kolacje", done: false },
];

function App() {
  const [hideOrShow, setHideOrShow] = useState(false);

  const toggleHideOrShow = () => {
    setHideOrShow((hideOrShow) => !hideOrShow);
  };
  return (
    <main className="container">
      <h1 className="container__header">Lista zadaÅ„</h1>

      <Section
        className="section__border"
        title="Dodaj nowe zadanie"
        taskContent={<Form />}
      />
      <Section
        className="section__border section__flex"
        title="Lista zadaÅ„"
        extraHeaderContent={
          <Buttons
            tasks={tasks}
            hideOrShow={hideOrShow}
            toggleHideOrShow={toggleHideOrShow}
          />
        }
        taskContent={<Tasks tasks={tasks} hideOrShow={hideOrShow} />}
      />
    </main>
  );
}

export default App;
